import axios from "axios";
import { 
    SIGN_UP_SUCCESS,
    SIGN_UP_FAIL,
    ACTIVATION_FAIL,
    ACTIVATION_SUCCESS,
    START1,

 } from "./types";

 const BASE_URL = 'http://127.0.0.1:8000';


 export const sign_up = (name, phone, email, password, re_password, is_alumni, is_student) => async dispatch => {
    dispatch({
        type: START1
    })
    
    const config = {
        headers : {
            'Content-Type': 'application/json',
        }
        }

    const body = JSON.stringify({name, phone, email, password, re_password, is_alumni, is_student})
    try{
        const res = await axios.post(`${process.env.REACT_APP_ENDPOINT_URL}/auth/users/`, body, config)
        console.log("See what is happening ", res)
        dispatch({
            type: SIGN_UP_SUCCESS,
            payload: res,
        })

    }catch(error){
        dispatch({
            type: SIGN_UP_FAIL,
            payload: error,
        })
    }
 }

 export const verify = (uid, token) => async dispatch => {
    dispatch({
        type: START1
    })

    const config = {
        headers : {
            'Content-Type': 'application/json'
        }
    }
    
    const body = JSON.stringify({uid, token})
    try{
         await axios.post(`${process.env.REACT_APP_ENDPOINT_URL}/auth/users/activation/`, body, config)

        dispatch({
            type: ACTIVATION_SUCCESS,
        })

    }catch(error){
        dispatch({
            type: ACTIVATION_FAIL,
        })
    }
 }