import React, {useState} from 'react'
import { Button } from '@mui/material';
import { Link,useNavigate} from 'react-router-dom';
import {connect} from 'react-redux';
import '../../../Assets/CSS/requests.css';
import SendIcon from '@mui/icons-material/Send';
import { reset_password } from '../../Actions/auth';



function PasswordResetRequest({ reset_password, resetMsg, isLoading}) { 
   const navigate = useNavigate()
   const [disable, setDisable] = useState(false)
   const [processing, setProcessing] = useState(false)
   const [requestSent, setRequestSent] = useState(false)

  const [formData, setFormData] = useState({
    "email": "",
  })

  const {email} = formData; // Destructure the state

  const handleChange = (e) => {
    setFormData({...formData, [e.target.name]:e.target.value}) 
  }
  const handleSubmit = (e) => {
    e.preventDefault();
    reset_password(email) //functonalities
    setRequestSent(true)
    setDisable(true)
    setProcessing(true) 

  } 

  console.log("Updates on variable", resetMsg)

  return (
    <div className='Requests'>
    <div className='cont'>
        <h3>Request Password Reset</h3>
        <div className='cont_card'>
                
            <form onSubmit={e => handleSubmit(e)}>

                <p>
                    Please enter your email address below.
                </p>
                <p style={{color: 'purple'}}>A mail with a link will be sent to your email. Click on the Link to 
                    reset your password</p>
                <section className="input_field">
                   <input onChange={(e) => handleChange(e) } type='email' placeholder='Your email' autoFocus="true" required
                  value={email}
                  name='email'
                /> 
                <Button disable={disable && processing}
                    type="submit" className='login__button'>
                        {isLoading ? "Sending..." : <span style={{color: 'purple'}}>Send</span>}
                </Button>
                </section>
                {
                  resetMsg?.status === 204 ? ( !isLoading ? <span className='errorP' style={{color: 'red'}}>Check your email and try again</span> : 
                  resetMsg === undefined && <span className='errorP'>Likely a connect problem, Check your connect status</span>  ) : ( "" )
                }

            </form>
        </div>
      </div>
    </div>
    
  )
}

const mapStateToProps = state => ({
  isLoading: state.auth.isLoading,
  resetMsg: state.auth.resetMsg,
})
export default connect(mapStateToProps, {reset_password})(PasswordResetRequest);