import { 
    SIGN_UP_SUCCESS,
    SIGN_UP_FAIL,
    ACTIVATION_SUCCESS,
    ACTIVATION_FAIL,
    START1,
 } from "../Actions/types";



 const initialState = {
    loading: false,
    isAuthenticated: false,
    throwback: '',
    error: '',
    reg_success: null,
 }


 export default function(state=initialState, action){
    const {type, payload} = action;
    switch(type){
        case START1:
            return{
                ...state,
                loading: true
            }
        case SIGN_UP_SUCCESS:
            return{
                ...state,
                isAuthenticated: false,
                loading: false,
                reg_success: true,
            }

        case ACTIVATION_SUCCESS:
            return{
                ...state,
                throwback: payload,
                loading: false,
                isAuthenticated: false,
            }

        case SIGN_UP_FAIL:
        case ACTIVATION_FAIL:
            return{
                ...state,
                error: payload,
                loading: false
            }
        default:
            return state;
    }
 }