import React, { useEffect, useRef, useState } from 'react'
import { connect } from 'react-redux';
import '../../Assets/CSS/Register.css';
import { Button } from '@mui/material';
import { Link, useNavigate } from 'react-router-dom';
import { sign_up } from '../Actions/auth_signup';
import {useFormik, validateYupSchema} from 'formik';
import * as Yup from 'yup'; 

import axios from 'axios';

function Register({sign_up, loading, throwback, error, reg_success}) {
    const inputRefs = useRef([])
    const [isLong, setIsLong] = useState("");
    const [matchErr, setMatchErr] = useState("");
    const [errors, setErrors] = useState("")
    const navigate = useNavigate();
    const [checkData, setCheckData] = useState({
        'is_alumni': false,
        'is_student': false
    })


    const handleKeyDown = (e, index) => {
        if(e.key === 'Enter' && e.key === "Tab"){
            e.preventDefault()
            const nextinputRef = inputRefs.current[index + 1];
            
            if(nextinputRef){
                nextinputRef.focus();
            }
        }
    };
    const [formData, setFormData] = useState({
        "name": "",
        "phone": "",
        "email": "",
        "password": "",
       "re_password": "",
    })

    const initialValues = {
        "name": "",
        "phone": "",
        "email": "",
        "password": "",
       "re_password": "",
       "is_alumni" : false,
       "is_student" : false,
    }

    const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/
    const passwordReg =  /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/

    const validationSchema = Yup.object({
        name : Yup.string().required("Please provide your name"),
        phone : Yup.string().required("Please provide your phone number").matches(phoneRegExp, "Invalid phone number input"),
        email : Yup.string().required("Please provide your email").email("Invalid email address"),
        password : Yup.string().required("Please provide a password").min(8, "passwords must be at least 8 characters").matches(passwordReg, 
            "Password must contain at least One Uppercase, One Lowercase, one number and  one Special Case Character").strict(true),
        re_password : Yup.string().label("confirm passwords").required().oneOf([Yup.ref('password'), null,], 'Passwords do not match'),
        checkOneAtATime : Yup.mixed().test("checkOneAtATime", "Please select one box", 
        function(value){
        const {is_alumni, is_student} = this.parent;
        return(is_alumni && !is_student) || ( !is_alumni && is_student) || (!is_alumni && !is_student)
        }),
    })

    const formik = useFormik({
		initialValues,
        validationSchema,

        onSubmit : async values => {
            const {name, phone, email, password, re_password, is_alumni, is_student} = values;
            sign_up(name, phone, email, password, re_password, is_alumni, is_student)
            if(reg_success === true){
                navigate("/Login")
             }else{
                navigate("")
             }
    }
	})

    console.log("throw back hre ", error)

    const handleAxiosErrors = (axiosErr) => {
    if(axiosErr?.code === ""){
    setErrors("Server error, please try again later")
    } else if(axiosErr?.code === ""){
    setErrors("Email or phone already in use")
    }else{
    setErrors("")
    }
}

  return (
    <div className='AuthRegister'>
       <div className="Register">
        <div className='register__card'>
            <h3> Create NFCS Account</h3>

            <form onSubmit={formik.handleSubmit}>
                {
                    error?.code === "ERR_NETWORK" ? (<span className='errorMsg'>Server error, Please try again later</span>):
                    ( error?.code ===  "ERR_BAD_REQUEST" ? <span className='errorMsg'>Email or phone already in use</span> : "" )
                }

                <label>Sign Up</label>
                <input type='text' placeholder='Name' enterKeyHint='true'
                ref={(el) => (inputRefs.current[0] = el)}
                onKeyDown={(e) => handleKeyDown(e, 0)}
                onChange={formik.handleChange}
                {...formik.getFieldProps("name")}
                name='name'
                />
                 {
                    formik.errors && formik.touched.name ? <span className="errorMsg">{formik.errors.name} </span> : ""
                }

                 <input type='text' placeholder='Phone' enterKeyHint='true'
                ref={(el) => (inputRefs.current[0] = el)}
                onKeyDown={(e) => handleKeyDown(e, 0)}
                onChange={formik.handleChange}
                {...formik.getFieldProps("phone")}
                name='phone'
                value={formik.values.phone}
                />

                {
                    formik.errors && formik.touched.phone ? <span className='errorMsg'>{formik.errors.phone}</span> : ""
                }


                <input type="email" placeholder='Email'  enterKeyHint='true' 
                ref={(el) => (inputRefs.current[1] = el)}
                onKeyDown={(e) => handleKeyDown(e, 1)}
                onChange={formik.handleChange}
                {...formik.getFieldProps("email")}
                name='email'
                />
                {
                    formik.errors && formik.touched.email ?  <span className='errorMsg'>{formik.errors.email}</span> : ""
                }
                <input type="password" placeholder='Password'
                ref={(el) => (inputRefs.current[2] = el)}
                onKeyDown={(e) => handleKeyDown(e, 2)}
                onChange={formik.handleChange}
                {...formik.getFieldProps("password")}
                name='password'
                />
                
                {
                    formik.errors && formik.touched.password ? <span className='errorMsg'>{formik.errors.password}</span> : ""
                }

                <input type="password" placeholder='Confirm Password'
                ref={(el) => (inputRefs.current[3] = el)}
                onKeyDown={(e) => handleKeyDown(e, 3)}
                onChange={formik.handleChange}
                {...formik.getFieldProps("re_password")}
                name='re_password'
                />
                {
                    formik.errors && formik.touched.re_password ? <span className="errorMsg">{formik.errors.re_password}</span> : ''
                }

                <div className='checkbox'>
                    <span className='row_input'>
                        <input type='checkbox' name='is_student' {...formik.getFieldProps("is_student") } onChange={formik.handleChange} required={false}/> 
                   <label htmlFor='is_student'>
                    Are you a Student?
                   </label>  
                    </span>

                    <span className='row_input'>
                        <input type='checkbox' name='is_alumni' {...formik.getFieldProps("is_alumni")}  onChange={formik.handleChange} required={false}/> 
                     <label htmlFor='is_alumni'>
                    Are you an Alumni?
                   </label>  
                    </span> 
                </div>
                {
                    formik.errors.checkOneAtATime && <span className='errorMsg'>{formik.errors.checkOneAtATime}</span>
                }
                <p className='visitor'>If you are a visitor, Please leave all the box blank</p>
                
                <Button type="submit">{ loading ? "Sending.." : "Register"}</Button>

                <p className='link_login'>Already Regisered? <Link to="/Login">Login here</Link></p>
            </form>
        </div>
    </div> 
    </div>
    
  )
}
const mapStateToProps = state => ({
    loading : state.auth_signup.loading,
    throwback: state.auth_signup.throwback,
    error: state.auth_signup.error,
    reg_success: state.auth_signup.reg_success,
})

export default connect(mapStateToProps , {sign_up})(Register);